.BuildControlls {
    width : 100% ;
    background-color: #8b5c8d;
    display: flex;
    flex-flow: column;
    align-items: center;
    box-shadow: 0 2px 2px #ccc;
    margin: auto;
    padding: 10px 0 ;
}

.OrderButton {
    background-color: #cb7dce;
    outline: none;
    cursor: pointer;
    border: 1px solid #380434;
    color: #380434;
    font-family: inherit;
    font-size: 1.2em;
    padding: 15px 30px;
    box-shadow: 2px 2px 2px #380434;
}

.OrderButton:hover, .OrderButton:active {
    background-color: #380434;
    border: 1px solid #cb7dce;
    color: #cb7dce;
}

.OrderButton:disabled {
    background-color: #C7C6C6;
    cursor: not-allowed;
    border: 1px solid #ccc;
    color: #888888;
}

.OrderButton:not(:disabled) {
    animation: enable 0.3s linear;
}

@keyframes enable {
    0% {
        transform: scale(1);
    }
    60% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }
}